.otherProjects {
    align-items: center;
    width: auto;
    margin: 50px auto;
}

.center-div {
    max-width: 450px;
    text-align: center;
    margin: 0 auto 100px auto;
}

.project-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
    margin: 50px auto;

    width: 940px;
}